*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}
.main_div{
  
    width: 100%;
    height: 100vh;
    /* transition: all 0.4s cubic-bezier(0.175,0.885,0,1); */
    background: #6983aa;
    /* width: 21.25%;
    height: auto;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(0,0,0,0.1); */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .center{
    width: 25%;
    height: 80vh;
    background-color: rgb(54, 53, 53);
    border-radius: 12px;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.3)
  }
  h1{
    color: white;
    background: transparent;
    background-color: #8566aa;
    padding: 6px 0 2px 0;
    margin-bottom: 10px;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.3)

  }

  input{
    text-align: center;
    height: 30px;
    top: 10px;
    border: none;
    outline: none;
    font-size: 18px;
    font-weight: 500;
    width: 60%;
    border-bottom: 2px solid #8566aa;
  }
  button{
    background-color: blueviolet;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    color: white;
    border: none;
    outline: none;
    margin-left: 10px;
    border-color: transparent;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.3)

  }
  
  button:hover{
    background-color: rgb(43, 248, 43);
  }

  ol {
    list-style-type: none;
   }

   li{
    text-align: left;
    margin-top: 0px;
    margin-left: 15px;
    font-size: 20px;
    font-weight: 400;
    color: white;
   }

   .cancel{
    background-color: #8566aa;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    font-weight: 1000;
   }
   

   .list{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
   }
   .list .fa-times{
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #8566aa;
    border-radius: 50%;
    margin: 4px 0px 0 0px;
    margin-left: 35px;
    color: aliceblue;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
   }

   .list button:hover {
    background-color: crimson;
   }
   

@media (max-width: 768px){
  .center{
    width: 55%;
  }
}

